import express from 'express';
import db from './db';
import ReqApi from './method/index';

const app = express();

app.get('/', function (req, res) {
    res.send('Hello World!!!');
});


app.get('/method/:method', function (req, res) {
    // console.log(req.query);

    let methodSplit = req.params.method.split(".");

    if(!methodSplit[0] || !methodSplit[1]) {
        return new Error();
    }

    let methodCat = methodSplit[0];
    let methodName = methodSplit[1];


    new ReqApi(methodCat);

    // db.put('users', [{
    //     id: 3884432,
    //     first_name: "Вася",
    //     last_name: "Петров"
    // }], function (err) {
    //     if (err) return console.log('Ooops!', err);
    // });

    db.get('users', function (err, value) {
        if (err) return console.log('Ooops!', err);
        console.log(value);

        res.send(value);
    });
});

const DEFAULT_PORT = parseInt(process.env.PORT, 10) || 3011;
app.listen(DEFAULT_PORT, function () {
    console.log('Starting server on port ' + DEFAULT_PORT);
});
